import React, { useState } from 'react'

import { MdOutlineDashboardCustomize } from "react-icons/md";

const menus = [
    { text: "Dashboard", icon: <MdOutlineDashboardCustomize className="text-xl" /> },
    { text: "Dashboard", icon: <MdOutlineDashboardCustomize className="text-xl" /> },
    { text: "Dashboard", icon: <MdOutlineDashboardCustomize className="text-xl" /> },
    { text: "Dashboard", icon: <MdOutlineDashboardCustomize className="text-xl" /> },
]

export default function Sidebar() {

    const [active, setActive] = useState("Dashboard");

    return (
        <div className="w-80 h-full bg-white">
            <div className="flex justify-center items-center h-20">
                <span className="text-lg font-semibold text-blue-600">CS SYSTEM</span>
            </div>

            <div className="flex flex-col items-center space-y-2">
                { menus.map((data,key) => (
                    <div 
                        key={key} 
                        className={"flex items-center w-10/12 py-3 px-4 gap-2 rounded-2xl "+(active===data.text ? "bg-blue-600 text-white" : "text-blue-500 hover:text-white hover:bg-blue-600")}
                    >
                        { data.icon } <span className="text-sm font-extralight">{ data.text }</span>
                    </div>
                )) }
            </div>
        </div>
    )
}
